@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
/* 300, 600, 800 */ /* 14px 16px */

*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	outline: 0;
	box-sizing: border-box;
	font-family: 'Nunito Sans', sans-serif;
}

:root {
	--app-background: #fafafa;
	--appbar: #ffffff;
	--element: #ffffff;
	--text: #111517;
	--input: #ffffff;
	--gradient: linear-gradient(
		to right,
		#d8d8d8 0%,
		#bdbdbd 20%,
		#d8d8d8 40%,
		#d8d8d8 100%
	);
}

:root[data-theme='dark'] {
	--app-background: #202c37;
	--appbar: #2b3945;
	--element: #2b3945;
	--text: #ffffff;
	--input: #858585;
	--gradient: linear-gradient(
		to right,
		#656871 0%,
		#888b94 20%,
		#656871 40%,
		#656871 100%
	);
}

html {
	scroll-behavior: smooth;
}

body {
	width: 100%;
	height: auto;
	min-height: 100vh;
}

div[id='root'] {
	width: 100%;
	height: auto;
	min-height: 100vh;
	background-color: #fafafa;
	background-color: var(--app-background);
}

@-webkit-keyframes shineEffect {
	0% {
		background-position: -468px 0;
	}
	100% {
		background-position: 468px 0;
	}
}

@keyframes shineEffect {
	0% {
		background-position: -468px 0;
	}
	100% {
		background-position: 468px 0;
	}
}

